import http from './interface'

/**
 * 
 *  url 
 * 
 */
const baseUrl = 'http://mall.zhihuiyunqi.com/wx'
// () import {test} from '@/common/vmeitime-http/'
export const httpget = (dataurl) => {
	http.interceptor.request = (config) => {
		config.header = {
			"content-type": "application/json",
			"X-Litemall-Token": uni.getStorageSync('token')
		}
	}
    return http.request({
		baseUrl: baseUrl,
        url: dataurl,
		method: 'get',
		dataType: 'json'
    })
}
export const httppost = (dataurl,data) => {
	http.interceptor.request = (config) => {
		config.header = {
			"content-type": "application/json",
			"X-Litemall-Token": uni.getStorageSync('token')
		}
	}
    return http.request({
		baseUrl: baseUrl,
        url: dataurl,
		method: 'post',
		dataType: 'json',
		data
    })
}

