<template>
	<view>
		<!-- #ifdef MP-WEIXIN -->
		<view class="cu-modal bottom-modal" :class="!haslogin && !hidemask?'show':''">
			<view class="cu-dialog">
				<view class="padding-xl" style="text-align:left;width: 100%;">
					<image src="/static/missing-face.png" style="width:80upx;height:80upx;border-radius: 100%;border:1px solid #ccc;"></image>
					<text style="position: relative;top: -30upx;left: 15upx;"></text>
					<view style="margin: 0 0 20upx;">
					<text style="font-size: 24upx;"></text></view>
					<!-- #ifdef MP-WEIXIN -->
					<button open-type="getUserInfo" @getuserinfo="getUserInfo" lang="zh_CN" class="cu-btn bg-green"></button>
					<!-- #endif -->
				</view>
			</view>
		</view>
		<!-- #endif -->
	</view>
</template>

<script>
	import loading from '@/components/loading.vue';
	import api from '@/common/vmeitime-http/'
	import tuiActionsheet from "@/components/actionsheet/actionsheet"

	import {
	    mapState, mapMutations
	} from 'vuex'
	export default {
		components: {
			loading, tuiActionsheet
		},
		data() {
			return {
				haslogin: true,
				agentId: 7813,
				showActionSheet: false,
				maskClosable: true,
				itemList: [],
				color: "#9a9a9a",
				size: 26,
				hidemask: false,
				isCancel: true
			};
		},
		created() {
			// #ifdef MP-WEIXIN
			if (uni.getStorageSync('haslogin') == '') {
				this.haslogin = false
			} 
			// #endif
			// #ifndef MP-WEIXIN
			 this.getUserInfo();
			// #endif
		},
		methods: {
			...mapMutations(['login']),
			itemClick: function(e) {
				let index = e.index;
				this.closeActionSheet();
			},
			openauth(){
				// #ifndef MP-ALIPAY
				this.haslogin = this.hidemask = false;
				// #endif
				// #ifdef MP-ALIPAY
				 this.getUserInfo();
				// #endif
			},
			closeActionSheet: function() {
				this.showActionSheet = false
			},
			getUserInfo: function() {
				uni.showLoading({title: ''})
				let that = this;
// #ifdef MP-ALIPAY
				my.getAuthCode({
					scopes: 'auth_user', // auth_userauth_base
					success: (res) => {
						if (res.authCode) {
							let code = res.authCode;
							// 
							uni.getUserInfo({
								provider: 'alipay',
								lang:'zh_CN',
								success: function(infoRes) {
									console.log(infoRes)
									let userInfo = infoRes.userInfo;
									var options = {
										code: code,
										userInfo: userInfo
									}
									that.login(userInfo)
									that.gologin(options)
								},
								fail: function() {
									uni.hideLoading()
									that.hidemask = true
									uni.navigateTo({
										url: '/pages/public/login'
									})
									console.log('')
								}
							})
						}
					},
					fail: function(loginRes) {
						if (uni.getStorageSync('haslogin') == '') {
							that.haslogin = false
							that.hidemask = true
						}
						uni.navigateTo({
							url: '/pages/public/login'
						})
						uni.hideLoading()
					}
				})
// #endif
// #ifndef MP-ALIPAY
				uni.login({
					provider: 'weixin',
					success: function(loginRes) {
						let code = loginRes.code;
						// 
						uni.getUserInfo({
							provider: 'weixin',
							lang:'zh_CN',
							success: function(infoRes) {
								let userInfo = infoRes.userInfo;
								var options = {
									code: code,
									userInfo: userInfo
								}
								that.login(userInfo)
								that.gologin(options)
							},
							fail: function() {
								uni.hideLoading()
								that.hidemask = true
								uni.navigateTo({
									url: '/pages/public/login'
								})
								console.log('')
							}
						})
					},
					fail: function(loginRes) {
						if (uni.getStorageSync('haslogin') == '') {
							that.haslogin = false
						}
					}
				})
// #endif
			},
			gologin(options, data) {
				let that = this;
				let loginurl = '/auth/login_by_weixin';
				api.httploginpost(loginurl, options).then((res) => {
					if (res.data.errno == 0) {
						uni.setStorageSync('token', res.data.data.token);
						that.haslogin = true
						that.hidemask = true
						uni.setStorageSync('haslogin', true);
						that.$parent.getdata();
					} else {
						uni.navigateTo({
							url: '/pages/public/login'
						})
					}
					uni.showToast({title: res.data.errmsg, icon: 'none'})
					uni.hideLoading()
				}).catch((err) => {
					uni.hideLoading()
				})
			}
		}
	}
</script>
 
